import { Point } from '../types';

// Funzione di supporto per calcolare la distanza euclidea
const distance = (p1: Point, p2: Point): number => {
  return Math.sqrt(Math.pow(p1.x - p2.x, 2) + Math.pow(p1.y - p2.y, 2));
};

export const imageEffects = [
  { name: 'Grigio', func: (ctx: CanvasRenderingContext2D) => {
    const imageData = ctx.getImageData(0, 0, ctx.canvas.width, ctx.canvas.height);
    for (let i = 0; i < imageData.data.length; i += 4) {
      const avg = (imageData.data[i] + imageData.data[i + 1] + imageData.data[i + 2]) / 3;
      imageData.data[i] = imageData.data[i + 1] = imageData.data[i + 2] = avg;
    }
    ctx.putImageData(imageData, 0, 0);
  }},
  { name: 'Inverti', func: (ctx: CanvasRenderingContext2D) => {
    const imageData = ctx.getImageData(0, 0, ctx.canvas.width, ctx.canvas.height);
    for (let i = 0; i < imageData.data.length; i += 4) {
      imageData.data[i] = 255 - imageData.data[i];
      imageData.data[i + 1] = 255 - imageData.data[i + 1];
      imageData.data[i + 2] = 255 - imageData.data[i + 2];
    }
    ctx.putImageData(imageData, 0, 0);
  }},
  { name: 'Voronoi', func: (ctx: CanvasRenderingContext2D) => {
    const width = ctx.canvas.width;
    const height = ctx.canvas.height;
    const imageData = ctx.getImageData(0, 0, width, height);
    const data = imageData.data;

    // Genera punti casuali per il diagramma di Voronoi
    const numPoints = 50;
    const points: Point[] = Array.from({ length: numPoints }, () => ({
      x: Math.random() * width,
      y: Math.random() * height
    }));

    // Applica l'effetto Voronoi
    for (let y = 0; y < height; y++) {
      for (let x = 0; x < width; x++) {
        let minDist = Infinity;
        let closestPoint: Point | null = null;

        for (const point of points) {
          const dist = distance({ x, y }, point);
          if (dist < minDist) {
            minDist = dist;
            closestPoint = point;
          }
        }

        if (closestPoint) {
          const index = (y * width + x) * 4;
          const colorIndex = (Math.floor(closestPoint.y) * width + Math.floor(closestPoint.x)) * 4;
          data[index] = data[colorIndex];
          data[index + 1] = data[colorIndex + 1];
          data[index + 2] = data[colorIndex + 2];
        }
      }
    }

    ctx.putImageData(imageData, 0, 0);
  }}
];